@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
body {
    @apply bg-gray-950 text-gray-50;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  }
}

@layer components {
  .btn-primary {
    @apply px-4 py-2 bg-darkPrimary-600 text-white rounded-md hover:bg-darkPrimary-700 focus:outline-none focus:ring-2 focus:ring-darkPrimary-500 focus:ring-offset-2 transition-colors duration-300;
  }
  
  .input-field {
    @apply block w-full rounded-md border-gray-700 bg-gray-800 text-gray-50 shadow-sm focus:border-darkPrimary-500 focus:ring-darkPrimary-500 transition-colors duration-300;
  }
  
  .card {
    @apply bg-gray-800 rounded-lg shadow-lg p-6;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Loading Spinner Styles */
.loader {
  border-top-color: #3498db; /* Blue color for the spinner */
  -webkit-animation: spin 1s linear infinite;
  animation: spin 1s linear infinite;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

body {
  font-family: 'Press Start 2P', 'pixel', monospace, sans-serif;
  background: #000;
  color: #fff;
  letter-spacing: 0.5px;
}

/* 顯性網格背景 */
.retro-grid {
  background-color: #000;
  background-image: linear-gradient(0deg, rgba(255,255,255,0.04) 1px, transparent 1px),
    linear-gradient(90deg, rgba(255,255,255,0.04) 1px, transparent 1px);
  background-size: 16px 16px;
}

/* 鋸齒邊緣效果 */
.retro-jagged {
  clip-path: polygon(
    0% 0%, 4% 8%, 8% 0%, 12% 8%, 16% 0%, 20% 8%, 24% 0%, 28% 8%, 32% 0%, 36% 8%, 40% 0%, 44% 8%, 48% 0%, 52% 8%, 56% 0%, 60% 8%, 64% 0%, 68% 8%, 72% 0%, 76% 8%, 80% 0%, 84% 8%, 88% 0%, 92% 8%, 96% 0%, 100% 8%, 100% 100%, 0% 100%
  );
}

/* CRT 濾鏡效果 */
.retro-crt {
  position: relative;
  overflow: hidden;
}
.retro-crt::before {
  content: '';
  pointer-events: none;
  position: absolute;
  top: 0; left: 0; right: 0; bottom: 0;
  background: repeating-linear-gradient(
    to bottom,
    rgba(255,255,255,0.04),
    rgba(255,255,255,0.04) 1px,
    transparent 1px,
    transparent 4px
  );
  z-index: 2;
}
.retro-crt::after {
  content: '';
  pointer-events: none;
  position: absolute;
  top: 0; left: 0; right: 0; bottom: 0;
  background: radial-gradient(ellipse at center, rgba(255,255,255,0.08) 0%, rgba(0,0,0,0.12) 100%);
  z-index: 2;
}

/* 像素化按鈕 */
.button-pixel {
  font-family: 'Press Start 2P', 'pixel', monospace, sans-serif;
  background: #000;
  color: #fff;
  border: 4px solid #fff;
  box-shadow: 4px 4px 0px 0px #ff00ff;
  padding: 12px 24px;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: box-shadow 0.1s, background 0.1s;
}
.button-pixel:hover {
  background: #ff00ff;
  color: #000;
  box-shadow: 4px 4px 0px 0px #00ffff;
}

/* 像素化輸入框 */
.input-pixel {
  font-family: 'Press Start 2P', 'pixel', monospace, sans-serif;
  background: #000;
  color: #fff;
  border: 4px solid #fff;
  box-shadow: 2px 2px 0px 0px #00ffff;
  padding: 8px 12px;
  outline: none;
  letter-spacing: 1px;
}
.input-pixel:focus {
  border-color: #ff00ff;
  box-shadow: 2px 2px 0px 0px #ff00ff;
}

/* 像素化卡片 */
.card-pixel {
  background: #111;
  border: 4px solid #fff;
  box-shadow: 8px 8px 0px 0px #00ffff;
  border-radius: 0;
  padding: 24px 16px;
  font-family: 'Press Start 2P', 'pixel', monospace, sans-serif;
}

/* 像素化標題 */
.heading-pixel {
  font-family: 'Press Start 2P', 'pixel', monospace, sans-serif;
  color: #ff00ff;
  text-shadow: 2px 2px 0px #00ffff;
  letter-spacing: 2px;
}

/* 像素化動畫 */
@keyframes pixel-blink {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.3; }
}
.blink {
  animation: pixel-blink 1s steps(2, start) infinite;
}
